body {
  margin: 0;
  padding:0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
}
.body__wrapper{
  background-color: black;
}
.light.header__wrapper{
  background-color: white;
}
.light-mode {
  background-color: white;
}
  
.title{
  color: #f8c1a3;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.btn:hover {
  background-color: #3e8e41;
  box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
}

.stock__price{
  color: #f8c1a3;
}
.login__container {
  
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 50%;
    height: 50%;
    border: 10px solid #f8c1a3;
    border-radius: 10px;
}

.stock__container{
  position: absolute;
  top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: 10px solid #f8c1a3;
    border-radius: 10px;
    width: 60%;
    height: 70%;
}

.login__title {
  font-size: 36px;
  margin-bottom: 20px;
  color: #f8c1a3;
  display:flex;
  justify-content: center;
  align-items: center;
}

.login__form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.login__input {
  padding: 10px;
  margin-bottom: 20px;
  font-size: 16px;
  width: 300px;
  border: none;
  border-bottom: 2px solid gray;
}

.login__button {
  padding: 10px 20px;
  background-color: #f8c1a3;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  width:300px;
  margin-bottom: 15px;

}

.login__guest-button {
padding: 10px 20px;
background-color: #f8c1a3;
border: none;
border-radius: 5px;
font-size: 16px;
cursor: pointer;
margin-top: 20px;
margin-bottom: 10px;
}

.login__logo {
  height: 75px;
  width: 75px;
  margin-right: 20px;
  }

  .search__logo {
    height: 100px;
