}

.header__wrapper {
  background-color: black;
  margin: 0;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
}

.header__logo {
  margin-left: 48px;
}

.header__search {
  display: flex;
  align-items: center;
  background-color: black;
  height: 39px;
  padding: 10px;
}

.header__searchContainer {
  display: flex;
  align-items: center;
  width: 100%;
  height: 20px;
  border: 1px solid #31363A;
  padding: 5px;
  border-radius: 4px;
}

.header__searchContainer > .MuiSvgIcon-root {
  color: gray;
  padding: 10px;
}

.header__searchContainer > input {
  border: none;
  width: 375px;
  margin-left: 5px;
  margin-right: 10px;
  background-color: black;
  color: white;
}

.header__searchContainer > input:focus {
  outline: none;
}

.header__menuItems a {
  font-weight: bold;
  color: white;
  text-decoration: none;
  padding: 0 10px;
  margin-right: 48px;
}

.header__menuItems a:hover {
  color: #f8c1a3;
  transition: all 0.2s ease-in-out;
  transform: scale(0.8);
  }
.header__menuItems a:active {
  color: #f8c1a3;
}

.stock__emoji{
